<template>
  <v-layout align-center justify-center class="mt-12 mx-5">
    <v-flex xs12 sm8 md4>
      <v-card class="elevation-12">
        <v-toolbar dark color="blue darken-3">
          <v-toolbar-title></v-toolbar-title>
        </v-toolbar>
        <v-card-text>
          <v-form>
            <v-text-field
              prepend-icon="mdi-account"
              name="login"
              label=""
              id="login"
              type="text"
            ></v-text-field>
            <v-text-field
              prepend-icon="mdi-lock"
              name="password"
              label=""
              id="password"
              type="password"
            ></v-text-field>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn dark text color="grey darken-3" @click="goBack"></v-btn>
          <v-btn dark color="blue darken-3" @click="toLogin"></v-btn>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
    export default {
        methods: {
          toLogin (){
            this.$store.commit('login_method');
            this.$router.push('/http')
          },
          goBack (){
            this.$router.go(-1)
          },
          getmd5 (username, pwd){
            let userpwd = username + pwd;
            let crypto = require('crypto');
            let md5 = crypto.createHash('md5');
            let res = md5.update(userpwd).digest('hex');
            return res
          }
        }
    }
</script>
